$countrydropdown:(

dropdownMenu:(
  margin: 0 auto,
  pad: 0,
  width: 100%,
  maxWidth: 254px,
  txtAlign: left,
  fntSize: 0.875em,
  mobmarginBottom: em(20),
  tabmarginBottom: em(30)
),

 dropdownList: ( 
    pos: relative,
    pad: 0,
    bgnd: $white,
    lstStyle: none,
    display: inline-block,
    width: 100%
 ), 

 dropdownSpan: (
    brdr: none,
    txtColor: $white,
    display: block,
    fntSize: em(22),
    pad: em(1) em(10),
    bgnd: $white,
    spantransition: all 0.2s ease-in,
    hoverbgnd:#c6c8c8,
    hoverColor: $deepBlue,
    spanContent: '\f078',
    fntFamily: "Font Awesome",
    pos: absolute,
    posTop: 0,
    posRight: 0,
    width: 33px,
    height: 30px,
    paddingTop: 3px,
    txtAlign: center,
    spanafterBgnd: #c6c8c8,
    spanaftertxtColor: $white,
    boxSizing: border-box
 ),

 dropdownUl: (
    pos: absolute,
    width: 100%,
    pad: 0,
    z-index: 999,
    height: 100px
 ),

 dropdownLink: (  
    display: block,
    pad: 6px 15px,
    textDecoration: none,
    txtColor: $black,
    transition: all 0.2s ease-in, 
    hoverBgnd: #c6c8c8,
    hovertxtColor: $black,
    fntSize: em(16),
    fntFamily: 'Arial'
 ),

 dropdownAll: (   
    point: cursor
 ),

  menuLevel: (
    display: none,
    menuopenDisplay: block,
    height: 5em,
    overflow: hidden,
    overflowY: scroll,
    boxShadow: inset 0 0 6px rgba(0, 0, 0, 0.3),
    webkitboxShadow: inset 0 0 6px rgba(0, 0, 0, 0.3),
    borderRadius: 0,
    bgndColor: $appleGreen,
    webkitscrollWidth: 7px,
    webkitscrollbgndColor: $white,
    webkitscrollHeight: 6px,
    yScroll: scroll

  ),
  dropdownActive: (
      brdr: none,
      txtColor: $black,
      display: block,
      fntSize: em(16),
      pad: 5.5px 15px,
      bgnd: $white,
      spantransition: all 0.2s ease-in,
      hoverbgnd:$white,
      hoverColor: $black,
      fontFamily: "Arial",
      fontWeight: $fontRegular
   )

);