
$videosLanding:(
	main:(
		isViewAllHide: false,
		isRoundedPlayIcon: true,
		playerBarBgnd: $white,
		playerBarHeight: 0
	),
	homePage:(
		bgnd: url('#{$brandImgPath}/header-green-bk-tile.jpg') repeat,
		padding: 0 0 1.5em 0,
		backgroundSize: 128px 134px,
		isPageBgnd: true,
		isBgnd: false, // title Bgnd
		playerPadding: 0,
	),
	videosPage:(

	),
	autoPlay: (
		bgColor: $white
	),
	autoPlayCheckBox: (
		bgColor: $brightGrey,
		isDefaultHeadingFont: true,
	),
	autoPlayLabel: (
		color: $black,
		isDefaultHeadingFont: true,
		padding: 3px 0 0 0,
		transform: uppercase,
		fontSize: 18px,
		leftVal: 8px,
		topVal: 3px
	),
	autoPlayOnOffButton: (
		bgColor: $white,
		activeBgnd: $appleGreen
	),
	autoPlayOnOffTxt: (
		color: $white
	),
	verticalPlaylist:(
		headingSize: em(30),
		headingSpacing: 0,
		scrollBarColor:$brightGrey,
		scrollThumbColor: $appleGreen
	),
	playIcon: (
        isRoundedCircle: true,
        bgnd:rgba($white,.8),
		color:rgba($black,.5),
		fontSize: 35px,
		tabFontSize: 30px,
		mobFontSize: 30px,
		blendMode: hard-light
    ),
	arrows:(
		color:null,
		mobColor: null,
		fontSize: null,
		background: null,
		isRoundBgnd: false,
		leftPos:20px,
		rightPos: 20px,
		opacity:1,
		sameAsCDAArrow: true
	),
	nowPlaying: (
		align: center,
		textTransform: none,
		padding: 4px 7px,
		margin: 0 auto,
		fontSize: em(21),
		letterSpacing: 0,
		backgroundImage: none,
		backgroundColor: $appleGreen,
		color: $white,
		fontFamily: "Arial",
		borderRadius: 4px
	),
	heading : (
		isDefaultHeadingFont: true,
        bgColor: transparent,
		color : $white,
        fontSize: null,
        fontFamily: null,
		fontWeight: null,
		align: left,
		margin:0,
		padding: 0,
		transform: null, //none
	),
    description : (
		isDefaultDesFont: true,
        bgColor: transparent,
        fontSize: em(20),
        color : $white,
        fontFamily: null,
		fontWeight: null,
		align: left,
		padding: 0,
		margin:em(5,20) 0 0,
	    mobilepadding:0
	),
	player:(
		paddingBottom:null, //calc(56.25% - 8px),
		border:null, //7px solid $violerRed,
		borderRadius: null,//4px
	),
	playerBar:(
		borderRadius: null, //20px,
		margin: null, //em(20) 0 0
	),
	playerBarHeading:(
		isDefaultHeadingFont: false,
		color : $brightGrey,
        fontSize: em(28),
        fontFamily: 'Arial',
		fontWeight: $fontBold,
		align: left,
		margin:0,
		padding: 0,
		letterSpacing: 0,
		transform: none
	),
	playerBarDescription:(
		isDefaultDesFont: true,
	  	fontSize: em(21),
        color : $brightGrey,
        fontFamily: null,
		fontWeight: null,
		align: left,
		padding: 0,
	    mobilepadding:0
	),
    thumbnailHeading: (
        bgColor: transparent,
		color : $white,
        fontSize: em(38),
        fontFamily: null,
		fontWeight: null,
		align: left,
		padding: 1rem 1em .5rem 3.375rem,
		tabPadding: 1rem 1em .5rem 3.375rem,
		mobPadding: em(20,38) em(15,38) 0 em(15,38),
		margin:  em(20,38) 0 0,
	),
	galleryTile: (
		tilePadding: null,
		tabTilePadding: 0,
		mobTilePadding: 0
	),
	listTitle : (
		bgColor: $white,
		color : $brightGrey,
        fontSize: em(21),
        fontFamily: 'Arial',
		fontWeight: $fontBold,
		align: left,
		margin: null, //em(15,28) 0 0 ,
		padding: em(15,28) em(20,28),
		borderRadius: null, //4px
		letterSpacing: 0,
		transform:none,
		mobAlign: left,
		mobPad: .85714em
	),
	listOverlay: (
		isOverlayImgAlone: false,
		backgroundColor: rgba($bay-blue, .7)
	),
	showMore: (
		margin:0,
		padding: null
	),
	videosDetail:(
		pageBgnd: $lilyWhite,
		isclearDiv: true,
		thumbHeadingColor: $fernGreen
	),
	categoryFilter:(
		sameAsThemeBtn3: true
	)
);
