.play-text {
    font-size: 1em;
    font-family: swiss;
    font-variant: normal;
    font-weight: normal;
    font-style: normal;
    text-align: right;
    white-space: normal;
    text-transform: none;
    text-decoration: none;
    line-height: normal;
    letter-spacing: normal
}




.swissBlackRounded {
    font-family: Swiss !important;
    font-weight: $fontRegular !important;
}

.swissBoldRounded {
    font-family: Swiss !important;
    font-weight: $fontBold !important;
}

.arialRounded {
    font-family: "Arial-Rounded", Arial, sans-serif !important;
    font-weight: $fontBold !important;
}


.loading-icon-extend {
    content: "\f110" !important;
    font-family: "Font Awesome";
    font-weight: 900;
    -webkit-font-smoothing: antialiased;
    display: inline-block !important;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    font-size: 56px;
    color: if(mapping($layout, main, loadingGIFColor), mapping($layout, main, loadingGIFColor), $violet);
    text-align: center;
    -moz-animation: fa-spin 2s infinite linear;
    -webkit-animation: fa-spin 2s infinite linear;
    animation: fa-spin 2s infinite linear;
}

.data-loading {
    position: relative;
    min-height: 31.71429em;
    float: left;
    width: 100%;
}

.data-loading:after {
    @extend .loading-icon-extend;
    margin-left: 5px;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    left: 0;
    right: 0;
    @if mapping($layout, main, isTransparenGIF) == true {
        background-color: transparent;
        width: auto;
        height: auto;
        bottom: auto;
    }
}

.next-data-loading {
    position: relative;
    overflow: hidden
}

.next-data-loading:after {
    @extend .loading-icon-extend;
    margin: 20px auto;
    width: 100%;
    margin: 20px 0;
    background: transparent;
    height: auto;
    position: static;
}

//buttons
//btn-colors
$icegreen:#C4D600;
$dark-pink:#f10196;

.btn-theme-play {
    color: $white;
    background-color: $pink;
    border-color: $icegreen;
    &:hover,
    &:focus

    /*, &.active*/
        {
        background-color: $icegreen;
        border-color: $pink;
    }
}

.btn-show-more {
    cursor: pointer;
    min-width: 240px;
    padding: 28px;
    border-width: 5px;
    line-height: 1.4em;
    display: inline-block;

    font-size: em(19);
    text-align: center;
    text-transform: uppercase;
    @include border-radius(em(50));
    border-style: solid;
    font-weight: $fontLight;

    &:before {
        font-family: 'Font Awesome';
        /*font-weight:700;*/
        content: "\f055";
        margin: 5px;
        vertical-align: middle;
    }

    &.active:before {
        content: "\f056";
    }

    @extend .btn-theme-play;

}

:link.btn-view-all {
    color: $pink;
}
.view-all{
    @media #{$small-down}{
        margin-right: mapping($layout,viewAll,parentMgnRghtMob);
    }
    margin-bottom: mapping($layout,viewAll,parentMgnBtm);
}
.btn-view-all {
    @if mapping($layout,viewAll,isOverwritePrimaryBtn) == true{
        @extend .theme-btn;
        background-color: mapping($layout,viewAll,bgndColor);
        border-color: mapping($layout,viewAll,borderColor);
        color: mapping($layout,viewAll,color);
        padding-left: mapping($layout,viewAll,padLft);
        padding-right: mapping($layout,viewAll,paddRght);
        &:hover{
            background-color: mapping($layout,viewAll,bgndColorHover);
            border-color: mapping($layout,viewAll,borderColorHover);
            color: mapping($layout,viewAll,colorHover);
        }
    } @else{
        cursor: pointer;
        max-width: 100%;
        min-width: 162px;
        margin: 0;
        padding: 0.78em 1.45em;
        border-width: em(3);
        display: inline-block;
    
        font-size: em(19);
        font-weight: $fontLight;
        white-space: normal;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        border-style: solid;
    
        border-color: $icegreen;
        color: $pink;
        background: $white;
    
        @include border-radius(2em !important);
        &:hover,
        &:focus
    
        /*, &.active */
            {
            border-color: $pink;
            color: $white;
            background: $icegreen;
        }
    }
}

