//margins
.margin-LR0 {
    margin-left: 0 !important;
    margin-right: 0 !important;
}
.video-page-title,
.play-page-title{
    &.no-image{
        &>div{
            width:100%;
            padding: mapping($layout,pageTitle,padding);
            @media #{$medium-down}{
                padding: mapping($layout,pageTitle,paddingTab);
            }
            @media #{$small-down}{
                padding: mapping($layout,pageTitle,paddingMob);
            }
        }
        @if mapping($layout, pageTitle, isAfterBgnd) == true {
            position: relative;
            display: inline-block;
            width: 100%;
            p:empty{
                display: none;
            }
            &:after{
                content: "";
                background: mapping($layout, pageTitle, afterBgnd);
                background-size:  mapping($layout, pageTitle, afterBgndSize);
                @if mapping($layout, pageTitle, isAfterBgndFixedBtm) == true {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: mapping($layout, pageTitle, afterBgndHght);
                }
            }
        }
    }
}

.global-mattel-header {
    .slick-arrow {
        font-size: 1em;
        top: 45%;
        background: transparent;

        &.slick-next:before,
        &.slick-prev:before {
            content: "\f359";
            @include font-awesome();
            color: $yellow;
            vertical-align: top;
            font-size: em(58);
            background: none;
            opacity: 1;

            @media #{$medium-down} {
                font-size: em(48);
            }
        }

        &.slick-next:before {
            content: "\f35a";
        }

    }

    .slick-prev:before,
    .slick-next:before {
        opacity: 1;
    }

    .slick-dots li {
        width: auto;
        height: auto;
        margin: 0;

        button {
            width: 18.5px;
            height: 18px;
            margin: 5px 9px;
            background-color: $athens-grey;
        }

        &.slick-active {
            button {
                background-color: $yellow;
            }
        }
    }
}

.slick-arrow-extend {
    .slick-arrow {
        font-size: 1em;
        width: 35px;
        height: 35px;

        &.slick-next:before,
        &.slick-prev:before {
            content: "\f104";
            @include font-awesome();
            color: $white;
            vertical-align: top;
            font-size: em(38);
            line-height: inherit;
            background: none;
        }

        &.slick-prev {
            left: -25px;
        }

        &.slick-next {
            right: -30px;

            &:before {
                content: "\f105";
            }
        }
    }
}

