/*
  Agency Fonts Usage
-------------------
 Light - 300
 Regular - 400
 Medium - 500
 Semi bold - 600
 Bold - 700
*/
// $fontPath : "../clientlibs/clientlib-maxsteel/resources/fonts";
$fontPath : "../resources/fonts";
// $fontPath : "../css/fonts";

/* Webfont: AgencyFB Black */
@font-face {
    font-family: 'AgencyFBBlack';
    src: url('#{$fontPath}/AgencyFB/AGENCYB_1.TTF') format('truetype'); /* Safari, Android, iOS */
    font-style: normal;
    font-weight: $fontBold;
    text-rendering: optimizeLegibility;
}

/* Webfont: AgencyFB Black */
@font-face {
    font-family: 'AgencyFBBold';
    src: url('#{$fontPath}/AgencyFB/AgencyFB_Black.ttf') format('truetype'); /* Safari, Android, iOS */
    font-style: normal;
    font-weight: $fontBold;
    text-rendering: optimizeLegibility;
}

/* Webfont: ArialMT */
@font-face {
    font-family: 'ArialMT';
    src: url('#{$fontPath}/arial_mt/ArialMT.eot'); /* IE9 Compat Modes */
    src: url('#{$fontPath}/arial_mt/ArialMT.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('#{$fontPath}/arial_mt/ArialMT.woff') format('woff'), /* Modern Browsers */
         url('#{$fontPath}/arial_mt/ArialMT.ttf') format('truetype'), /* Safari, Android, iOS */
         url('#{$fontPath}/arial_mt/ArialMT.svg#ArialMT') format('svg'); /* Legacy iOS */
    font-style: normal;
    font-weight: $fontRegular;
    text-rendering: optimizeLegibility;
}

/* Webfont: ArialRoundedMTBold */
@font-face {
    font-family: 'ArialRoundedMTBold';
    src: url('#{$fontPath}/arial_rounded/ArialRoundedMTBold.eot'); /* IE9 Compat Modes */
    src: url('#{$fontPath}/arial_rounded/ArialRoundedMTBold.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
         url('#{$fontPath}/arial_rounded/ArialRoundedMTBold.woff') format('woff'), /* Modern Browsers */
         url('#{$fontPath}/arial_rounded/ArialRoundedMTBold.ttf') format('truetype'), /* Safari, Android, iOS */
         url('#{$fontPath}/arial_rounded/ArialRoundedMTBold.svg#ArialRoundedMTBold') format('svg'); /* Legacy iOS */
    font-style: normal;
    font-weight: $fontRegular;
    text-rendering: optimizeLegibility;
}
