$imagetextcomponent:(

imagetextTitle:(
    pad: em(10) 0,
    display: block,
    float:left,
    width: 100%,
    titlePadding: em(25)
),

 imagetextContainer: ( 
    margin: 0,
    pad: em(55) em(40),
    tabPad: null,
    mobPad: em(30) em(15),
    width: 100%,
    bgndDetail: none,
    imgOrderChangeDevices: true // for first component in a page
 ), 
 imageBlockImg: (
  width: 80%,
  widthTab: auto,
  widthMob: 100%,
  isImageCenterDevice: true
 ),
 imagetextblock: (
  pad: 0 em(40),
  tabPad: em(15) em(15) 0,
  mobPad: em(15) em(15) 0
 ),

 imagetextblockH2: (
    txtColor: $fernGreen,
    smalltxtColor: $white,
    margin:0,
    fntSize: em(50),
    tabFontSize: em(50),
		mobFontSize: em(45),
 ),

 imagetextblockP: (
   txtColor: $brightGrey,
   fntFamily: 'Arial',
   fntSize: em(21),
   tabFontSize: em(21),
		mobFontSize: em(21),
 ),

 imagetextblockLink: (  
   txtColor: $violet,
   txtDecoration: underline  
 ),

 imagetextCta: (   
     txtAlignLeft: left,
     txtAlignRight : right,
     txtAlignCenter: center,
     tabtxtAlign: left !important
  ),

  textcomp: (
    margin:0 !important
  )

);