$layout:(
	body:(
		font: "Arial",
		fontWeight: $fontRegular,
		bgnd: null,
		bgndPosition: null,
		bgndSize: null
	),
	main: (
		titleMargin: 3.125rem 3.375rem 3.125rem 3.375rem,
		tabTitleMargin: 1.875rem,
		mobTitleMargin: 1.25rem,
		sectionMargin: 0rem 3.375rem 0 3.375rem,
		tabSectionMargin: 0rem 1.875rem,
		mobSectionMargin: 0rem 0.9375rem,
		loadingGIFColor: $appleGreen,
		sectionTopPadding: em(90.5),
		sectionTopPaddingMob: em(90.5),
	),
	pageTitle:(
		padding: em(28) 0 em(35),
		paddingTab: em(28) 0 em(35),
		paddingMob: em(28) 0,
		isAfterBgnd: true,
		isAfterBgndFixedBtm: true,
		afterBgnd: transparent url("#{$brandImgPath}/black-border.png") repeat-x,
		afterBgndHght: 11px
		// afterBgndSize: 
	),
	heading:(
		font: "AgencyFBBlack",
		fontWeight: $fontBold,
		fontSize: em(50),
		color: $fernGreen,
		align: center
	),
	description:(
        font:"Arial",
        fontWeight:$fontRegular,
        fontSize: em(24)
    ),
	themeBtn:(
        isDefaultHeadingFont: true,
		fontSize: em(30),
		mobFontSize : em(25),
		padding: em(10.5, 30) em(15, 30),
		fontFamily:null,
        fontWeight:null,
        iscurveShape:false,
        bgndColor:$brightGrey,
        hoverBgndColor:$shuttle-grey !important,
        clipPath:none, //polygon(10% 0 100% 0 100% 100% 0 100% 0 40%),
        color:$white,
        hoverColor:$white !important,
		border: 2px solid $brightGrey !important,
		borderRadius: 0,
		letterSpacing: 0,
		isnextArrw: false,
		nextContent:"",
		transform: uppercase,
		contentFamily:"",
		contentWeight: "",
		contentMargin: ""
	),
	secThemeBtn:(
		isSecondaryBtn: true,
		bgndColor: $brightGrey, //$white,
		isArrow: false,
		padding: em(13, 30) em(38, 30) em(15, 30) em(30, 30),
		isPlus: true,
		isFontAwesomeIcon: true,
		iconContent: '\f055',
		iconHoverContent: '\f056',
		ArrowtopVal: 2px,
		marginRghtVal: 15px,
		hoverBgndColor:$shuttle-grey, //$dollyGreen,  
		color: null, //$violerRed,
		hoverColor: null, //$violerRed !important,
		border: null, //2px solid $violerRed,
		ArrowfontSize: 95%,
		ArrowlineHeight: .2
	),
	themeBtn3:(
		fontSize: em(30),
		mobFontSize : em(25),
		padding: em(15, 30) em(45, 30) em(13, 30) em(20, 30),
		fontFamily:null,
		fontWeight: null,
		iscurveShape: true,
		bgndColor: $brightGrey,
		hoverBgndColor: $fernGreen,  
		clipPath: polygon(0 0,0 100%,100% 100%,100% 50%,80% 0),
		color: $white,
		hoverColor: $white !important,
		borderSize: 0,
		borderRadius: 0,
		letterSpacing: 0,
		isnextArrw: false,
		transform: uppercase
	),
	viewAll: (
		isOverwritePrimaryBtn: true,
		bgndColor: #c6c8c8,
        borderColor: #c6c8c8 !important,
		color: #595b5d !important,
		bgndColorHover: #c6c8c8,
        borderColorHover: #c6c8c8 !important,
        colorHover: #595b5d !important,
        padLft: 30px,
		paddRght: 30px,
		parentMgnRghtMob: 0 !important,
		parentMgnBtm: 0 !important
	),
	dots:(
		fontSize: 14px,
        opacity:1,
        focusColor:$light-green,
        bgndColor:rgba($lightGreen, .4),
        activeBgndColor:$lightGreen,
        margin:0 6px !important,
		bottomPositon: 33px,
	),
	arrow: (
		color:rgba($lilyWhite,.7),
        opacity: .7,
		isCircleBgnd: true,
		circleBgnd: rgba($black, .7),
		circleHoverBgnd: rgba($black, .7)
  ),
  gototop: (
        bgndColor:#595b5d,
        brdrColor:#6cc04a 2px solid,
        fntColor:$white,
        fntFamily:AgencyFBBlack,
        fntSize:em(20),
		arrowShape:10px solid $white,
		width: 89px,
		height: 89px,
		lineHeight: 1.1
  ),
  filterTab: (
	  fntSize: em(30),
	  fntSizeTab: em(30),
	  fntSizeMob: em(21),
	  padding: 1.3125rem,
	  isBgnd: true,
	  bgnd: transparent url('#{$brandImgPath}/tab_default.png') no-repeat 100% 100%,
	  bgndMob: transparent,
	  bgndSize: 100% 100%,
	  bgndHover: transparent url('#{$brandImgPath}/tab_active.png') no-repeat 100% 100%,
	  bgndSizeHover: cover,
	  color: $white,
	  colorMob: $brightGrey,
	  colorHover: $white,
	  fntFamilyMob: Arial,
	  fntWeightMob: $fontBold,
	  transform: uppercase,
	  transformMob: none
  )
);