$header:(
  main:(
    bgnd: $fernGreen, //$deepBlue,
    boxShadow: 0 3px 5px 0 rgba($black,.74),
    minHeight: em(90.5),
    posTop: 3.6em,
    posLeft:0,
    zIndex: 1025,
    pos: fixed,
    width: 100%,
    lineHeight:em(14),
    bgndSize: contain,  
    mobBgndSize: cover
  ),
  childDiv: (
    width: auto
  ),
  logo:(
    pos: absolute,
    zIndex: 9999
  ),
  logoImage:(
    margin:em(12) 0 0 em(9),
    width: em(137), //mob
    mobMargin:em(13) 0 0 em(42),
    mobWidth: em(170.5), //des
    tabMargin: em(9) 0 0 em(35),
    tabWidth: em(148), //tab
  )
);
